import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '@/pages/home'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			name: '',
			component: Home,
			children: [
				{
					path: '*',
					name: 'Hello',
					component: Hello
				}
			]
		},
	]
})



